@import url(//db.onlinewebfonts.com/c/ac6286065aab4824af64a06aa5467f04?family=Swiss);
body {
	margin: 0;
	font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
		'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
		sans-serif;
	-webkit-font-smoothing: antialiased;
	-moz-osx-font-smoothing: grayscale;
}

code {
	font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
		monospace;
}
@font-face {
	font-family: 'Swiss 721';
	src: url('//db.onlinewebfonts.com/t/ac6286065aab4824af64a06aa5467f04.eot');
	src: url('//db.onlinewebfonts.com/t/ac6286065aab4824af64a06aa5467f04.eot?#iefix')
			format('embedded-opentype'),
		url('//db.onlinewebfonts.com/t/ac6286065aab4824af64a06aa5467f04.woff2')
			format('woff2'),
		url('//db.onlinewebfonts.com/t/ac6286065aab4824af64a06aa5467f04.woff')
			format('woff'),
		url('//db.onlinewebfonts.com/t/ac6286065aab4824af64a06aa5467f04.ttf')
			format('truetype'),
		url('//db.onlinewebfonts.com/t/ac6286065aab4824af64a06aa5467f04.svg#Swiss 721')
			format('svg');
}

:root {
	--mainBlue: #2a2a72;
	--lightBlue: #009ffd;
	--mainWhite: #f3f3f3;
	--mainDark: #232528;
	--mainYellow: #ffa400;
}
body {
	font-family: 'Rajdhani', sans-serif !important;
	background: #f3f3f3 !important;
	background: var(--mainWhite) !important;
	color: #232528 !important;
	color: var(--mainDark) !important;
}
.text-title {
	font-family: 'Open Sans Condensed', sans-serif;
	font-size: 1.6rem;
	text-transform: uppercase;
}
.text-blue {
	color: #2a2a72;
	color: var(--mainBlue);
}
.text-bright {
	color: #009ffd;
	color: var(--lightBlue);
}
.btn-black {
	background: transparent;
	text-transform: capitalize;
	font-size: 1rem !important;
	color: #232528;
	color: var(--mainDark);
	border-radius: 0 !important;
	border: 0.1rem solid #232528 !important;
	border: 0.1rem solid var(--mainDark) !important;
}
.btn-black:hover {
	background-color: #232528 !important;
	background-color: var(--mainDark) !important;
	color: #f3f3f3;
	color: var(--mainWhite);
}
.cart-icon {
	cursor: pointer;
	font-size: 1rem;
	color: #ffa400;
	color: var(--mainYellow);
}

.site-footer {
	background-color: #26272b;
	padding: 45px 0 20px;
	font-size: 15px;
	line-height: 24px;
	color: #737373;
}
.site-footer hr {
	border-top-color: #bbb;
	opacity: 0.5;
}
.site-footer hr.small {
	margin: 20px 0;
}
.site-footer h6 {
	color: #fff;
	font-size: 16px;
	text-transform: uppercase;
	margin-top: 5px;
	letter-spacing: 2px;
}
.site-footer a {
	color: #737373;
}
.site-footer a:hover {
	color: #3366cc;
	text-decoration: none;
}
.footer-links {
	padding-left: 0;
	list-style: none;
}
.footer-links li {
	display: block;
}
.footer-links a {
	color: #737373;
}
.footer-links a:active,
.footer-links a:focus,
.footer-links a:hover {
	color: #3366cc;
	text-decoration: none;
}
.footer-links.inline li {
	display: inline-block;
}
.site-footer .social-icons {
	text-align: right;
}
.site-footer .social-icons a {
	width: 40px;
	height: 40px;
	line-height: 40px;
	margin-left: 6px;
	margin-right: 0;
	border-radius: 100%;
	background-color: #33353d;
}
.copyright-text {
	margin: 0;
}
@media (max-width: 991px) {
	.site-footer [class^='col-'] {
		margin-bottom: 30px;
	}
}
@media (max-width: 767px) {
	.site-footer {
		padding-bottom: 0;
	}
	.site-footer .copyright-text,
	.site-footer .social-icons {
		text-align: center;
	}
}
.social-icons {
	padding-left: 0;
	margin-bottom: 0;
	list-style: none;
}
.social-icons li {
	display: inline-block;
	margin-bottom: 4px;
}
.social-icons li.title {
	margin-right: 15px;
	text-transform: uppercase;
	color: #96a2b2;
	font-weight: 700;
	font-size: 13px;
}
.social-icons a {
	background-color: #eceeef;
	color: #818a91;
	font-size: 16px;
	display: inline-block;
	line-height: 44px;
	width: 44px;
	height: 44px;
	text-align: center;
	margin-right: 8px;
	border-radius: 100%;
	transition: all 0.2s linear;
}
.social-icons a:active,
.social-icons a:focus,
.social-icons a:hover {
	color: #fff;
	background-color: #29aafe;
}
.social-icons.size-sm a {
	line-height: 34px;
	height: 34px;
	width: 34px;
	font-size: 14px;
}
.social-icons a.facebook:hover {
	background-color: #3b5998;
}
.social-icons a.twitter:hover {
	background-color: #00aced;
}
.social-icons a.linkedin:hover {
	background-color: #007bb6;
}
.social-icons a.dribbble:hover {
	background-color: #ea4c89;
}
@media (max-width: 767px) {
	.social-icons li.title {
		display: block;
		margin-right: 0;
		font-weight: 600;
	}
}

